@mixin filled-button($background, $color) {
  background-color: $background;
  color: $color;
}

@mixin outlined-badged {
  cursor: pointer;
  float: right;
  margin-right: 5px;
  margin-left: 5px;
  background: transparent;
  color: var(--color_siesa_2);
  border: 1px solid;
  &:hover {
    @include filled-button( var(--color_siesa_2), #fff);
  }
}

.oreports-popover{
  width: 250px;
  .react-datepicker-wrapper{
    width: 100%;
  }
  form span.edit-function-bagged-popover {
    @include outlined-badged;
  }
}

.source-tree{
  padding: 10px;
  border: 0.5px solid #70707052;
  border-radius: 7px;
  min-height: 200px;
  max-height: 200px;
  display: flex;
  flex-direction: column;
  overflow: auto;

  .tree-container{
    overflow: auto;
    margin-top: 5px;
  }
  >.col-md-6{
    flex-shrink: 0;
  }

  >.col-md-6, .col-md-12{
    padding: 0;
  }

  .rc-tree {
    padding: 0;
    padding-top: 5px;
  }

  .rc-tree li {
    padding: 0;
    margin: 0;
    list-style: none;
    white-space: nowrap;
    outline: 0;
  }
  .rc-tree li .draggable {
    color: #333;
    -webkit-user-select: none;
    user-select: none;
    /* Required to make elements draggable in old WebKit */
    -khtml-user-drag: element;
    -webkit-user-drag: element;
  }
  .rc-tree li.drag-over > .draggable {
    background-color: #316ac5;
    color: white;
    border: 1px #316ac5 solid;
    opacity: 0.8;
  }
  .rc-tree li.drag-over-gap-top > .draggable {
    border-top: 2px blue solid;
  }
  .rc-tree li.drag-over-gap-bottom > .draggable {
    border-bottom: 2px blue solid;
  }
  .rc-tree li.filter-node > .rc-tree-node-content-wrapper {
    color: #a60000!important;
    font-weight: bold!important;
  }
  .rc-tree li ul {
    margin: 0;
    padding: 0 0 0 18px;
  }
  .rc-tree li .rc-tree-node-content-wrapper {
    display: inline-block;
    padding: 1px 3px 0 0;
    margin: 0;
    cursor: pointer;
    height: 17px;
    text-decoration: none;
    vertical-align: top;
  }
  .rc-tree li span.rc-tree-switcher,
  .rc-tree li span.rc-tree-checkbox,
  .rc-tree li span.rc-tree-iconEle {
    line-height: 16px;
    margin-right: 2px;
    width: 16px;
    height: 16px;
    display: inline-block;
    vertical-align: middle;
    border: 0 none;
    cursor: pointer;
    outline: none;
    background-color: transparent;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-image: url('./images/treeicons.png');
  }
  .rc-tree li span.rc-tree-switcher.rc-tree-icon__customize,
  .rc-tree li span.rc-tree-checkbox.rc-tree-icon__customize,
  .rc-tree li span.rc-tree-iconEle.rc-tree-icon__customize {
    background-image: none;
  }
  .rc-tree li span.rc-tree-icon_loading {
    margin-right: 2px;
    vertical-align: top;
    background: url('data:image/gif;base64,') no-repeat scroll 0 0 transparent;
  }
  .rc-tree li span.rc-tree-switcher.rc-tree-switcher-noop {
    cursor: auto;
  }
  .rc-tree li span.rc-tree-switcher.rc-tree-switcher_open {
    background-position: -93px -56px;
  }
  .rc-tree li span.rc-tree-switcher.rc-tree-switcher_close {
    background-position: -75px -56px;
  }
  .rc-tree li span.rc-tree-checkbox {
    width: 13px;
    height: 13px;
    margin: 0 3px;
    background-position: 0 0;
  }
  .rc-tree li span.rc-tree-checkbox-checked {
    background-position: -14px 0;
  }
  .rc-tree li span.rc-tree-checkbox-indeterminate {
    background-position: -14px -28px;
  }
  .rc-tree li span.rc-tree-checkbox-disabled {
    background-position: 0 -56px;
  }
  .rc-tree li span.rc-tree-checkbox.rc-tree-checkbox-checked.rc-tree-checkbox-disabled {
    background-position: -14px -56px;
  }
  .rc-tree li span.rc-tree-checkbox.rc-tree-checkbox-indeterminate.rc-tree-checkbox-disabled {
    position: relative;
    background: #ccc;
    border-radius: 3px;
  }
  .rc-tree li span.rc-tree-checkbox.rc-tree-checkbox-indeterminate.rc-tree-checkbox-disabled::after {
    content: ' ';
    transform: scale(1);
    position: absolute;
    left: 3px;
    top: 5px;
    width: 5px;
    height: 0;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
  }
  .rc-tree:not(.rc-tree-show-line) .rc-tree-switcher-noop {
    background: none;
  }
  .rc-tree.rc-tree-show-line li:not(:last-child) > ul {
    background: url('data:image/gif;base64,R0lGODlhCQACAIAAAMzMzP///yH5BAEAAAEALAAAAAAJAAIAAAIEjI9pUAA7') 0 0 repeat-y;
  }
  .rc-tree.rc-tree-show-line li:not(:last-child) > .rc-tree-switcher-noop {
    background-position: -56px -18px;
  }
  .rc-tree.rc-tree-show-line li:last-child > .rc-tree-switcher-noop {
    background-position: -56px -36px;
  }
  .rc-tree-child-tree {
    display: none;
  }
  .rc-tree-child-tree-open {
    display: block;
  }
  .rc-tree-treenode-disabled > span:not(.rc-tree-switcher),
  .rc-tree-treenode-disabled > a,
  .rc-tree-treenode-disabled > a span {
    color: #767676;
    cursor: not-allowed;
  }
  .rc-tree-node-selected {
    background-color: #ffe6b0;
    border: 1px #ffb951 solid;
    opacity: 0.8;
  }
  .rc-tree-icon__open {
    margin-right: 2px;
    background-position: -110px -16px;
    vertical-align: top;
  }
  .rc-tree-icon__close {
    margin-right: 2px;
    background-position: -110px 0;
    vertical-align: top;
  }
  .rc-tree-icon__docu {
    margin-right: 2px;
    background-position: -110px -32px;
    vertical-align: top;
  }
  .rc-tree-icon__customize {
    margin-right: 2px;
    vertical-align: top;
  }
  

  .rc-tree-iconEle{
    display: none !important;
  }

  .rc-tree-title{
    font-size: 12px;
    color: #053970; 
    font-weight: 500;
  }

  .rc-tree-treenode-checkbox-checked{
    >.rc-tree-node-content-wrapper>.rc-tree-title{
      font-weight: 600;
    }
  }
}

.root_oreports {

  .oreports-container {
    background-color: #fff;
    max-height: calc(100vh - 250px);
    height: 100%;
    min-height: 400px;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
  }

  .oreports-header {
    background-color: #053970;
    max-height: 70px;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    //position: relative;
    padding: 15px;

    justify-content: space-between;
    display: flex;
    flex-direction: row;

    >* {
      color: #fff;
    }

    .info {
      flex-direction: column;
      display: flex;
    }

    .title {
      // position: absolute;
      // top: 50%;
      // transform: translateY(-50%);
      font-weight: bold;
      font-size: 16px;
    }

    .header-options {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;

      i {
        margin-left: 10px;
        font-size: 20px;
        cursor: pointer;
      }
    }
  }

  .oreports-body {
    display: flex;
    flex-direction: row;
    height: 100%;

    .sidebar-header {
      display: flex;
      flex-direction: row;
      height: 35px;
      border-bottom: solid #dddddd 1px;

      .sidebar-icon, .sidebar-collapse {
        background-color: var(--color_primary);
        padding: 10px;
        // border: 0.5px solid #9C9C9C;
        // border-top-width: 0;
        // border-bottom-width: 0;
        &:first-child{
          border-top-left-radius: 7px;
        }
        cursor: pointer;

        >I {
          color: #fff;
          font-size: 18px;
        }
      }

      .sidebar-collapse {
        background-color: #fff;
        >I {
          color: #d6d6d6;
          font-size: 18px;
        }
      }

      .sidebar-title {
        // border: 0.5px solid #9C9C9C;
        flex: 1;
        justify-content: center;
        align-items: center;
        display: flex;

        >I {
          margin-right: 5px;
        }
      }
    }

    .height-100{
      height: 100%;
    }

    .oreports-sidebar {
      flex: 1;
      min-width: 250px;
      // background-color: magenta;
      height: 100%;
      border-bottom-left-radius: 7px;
      overflow: auto;
      border-right: solid #dddddd 1px;

      // &:hover {
      //   overflow: auto;
      // }

      &.collapsed {
        min-width: unset;
        flex:unset;
      }

      .fields-main-container {
        min-height: 250px;
        max-height: 400px;
        display: flex;
        flex-direction: column;

        .fields-container {
          overflow: auto;
          flex: 1;

          // &:hover {
          //   overflow: auto;
          // }
          max-height: 100%;
          // background-color: aqua ;
          display: flex;
          flex-direction: column;
          margin-top: 10px;
          margin-bottom: 10px;
        }
        .fields-title-container {
          display: flex;
          // margin-bottom: 10px;
          .fields-title-action-container {
            margin-left: auto;
          }
          .fields-title-label-container label {
            margin-bottom: 0px;
            margin-top: 10px;
          }
        }
      }
    }

    .oreports-table {
      // background-color: yellow;
      flex: 5;
      border-bottom-right-radius: 7px;
      overflow: hidden;

      &:hover {
        overflow: auto;
      }
    }
  }

  .table-container {
    display: flex;
    flex-direction: column;
    height: 100%;

    .table-footer {
      max-height: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-top: solid #dddddd 1px;

      .info-message {
        color: var(--color_siesa_2);
        font-size: 13px;
      }
    }
  }

  .table-empty-placeholder {
    height: 100%;
    border-top-right-radius: 7px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;

    span {
      margin-top: 15px;
      font-size: 16px;
    }

    svg {
      max-width: 50%;
    }
  }

  .draggable-field-container {
    margin-bottom: 5px;
  }

  .draggable-field-panel {
    padding: 5px;
    border: 1px solid #868686;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;

  }

  .draggable-field-label {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .draggable-field {
    background-color: #868686;
    border-radius: 5px;
    color: #fff;
    justify-content: space-between;
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
  }

  .dx-datagrid .dx-row-alt>td {
    background-color: #DFE6EF;
  }

  #tableContainer {
    height: 100%;

    .dx-datagrid-total-footer>.dx-datagrid-content {
      padding-top: 0;
      padding-bottom: 0;
    }

    .dx-gridbase-container {
      border-top-right-radius: 7px;
    }
  }

  .box-bordered {
    border: 1px dashed var(--color_primary);
    flex: 1;
  }

  .no-border-bottom-radius {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }

}

.modal-open .container-fluid,
.modal-open .container {
  filter: blur(5px) grayscale(90%);
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .config-modal {
    .modal-content {
      min-width: 100%;
    }
  }
}
.modal-backdrop {
  background-color: #fff !important;
}

.oreports-primary-button {
  background-color: var(--color_siesa_2);
  border: var(--color_siesa_2) !important;
  cursor: pointer;
  color: #fff;

  &:hover {
    background-color: #009c91;
  }
}

.oreports-primary-button.outlined {
  background-color: transparent;
  color: var(--color_siesa_2) !important;
  border: 1px solid var(--color_siesa_2) !important;

  &:hover {
    color: #fff;
    background-color: #009c91;
  }

}

.config-modal {
  justify-content: center;
  .modal-content {
    //border-color: var(--color_primary);
    border: none;
    min-width: 900px;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
    box-shadow: 5px 5px 10px #00000029;
  }

  .modal-header {
    background-color: var(--color_primary_dark);
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    color: #fff;

    .close {
      color: #fff;
    }
  }

  .modal-footer {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;

    &:before,
    &:after {
      display: none;
    }
  }
}

@media screen and (max-width: 800px) {
  .config-modal {
    .modal-content {
      min-width: 0;
    }
  }
}

